export function _getDonations() {
  return new Promise((resolve) => {
    setTimeout(() => resolve({ ...donations }), 2000);
  });
}

export function _getEvents() {
  return new Promise((resolve) => {
    setTimeout(() => resolve({ ...events }), 2000);
  });
}

export function _getArticles() {
  return new Promise((resolve) => {
    setTimeout(() => resolve({ ...articles }), 2000);
  });
}
